import http from 'src/utils/http';

export function getCategory() {
  return http.get('/metadata/category');
}

export function create(param) {
  return http.post('/metadata/category', param);
}

export function update(param) {
  return http.post('/metadata/update/category', param);
}

export function remove(categoryGuid) {
  return http.delete(`/metadata/category/${categoryGuid}`);
}




