import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as relationService from 'src/service/relation';

const {
  GET_RELATION,
  GET_RELATION_SUCCESS,
  GET_RELATION_FAILURE,

  GET_ALL_RELATION,
  GET_ALL_RELATION_SUCCESS,
  GET_ALL_RELATION_FAILURE,

  SEARCH_RELATION,
  SEARCH_RELATION_SUCCESS,
  SEARCH_RELATION_FAILURE,

  // create
  CREATE_RELATION,
  CREATE_RELATION_SUCCESS,
  CREATE_RELATION_FAILURE,

  // delete
  DELETE_RELATION,
  DELETE_RELATION_SUCCESS,
  DELETE_RELATION_FAILURE
} = types;

// initial state
const state = {
  currentRelations: {
    list: [],
    total: 0
  }, // 当前选中节点的当前页关联列表
  currentAllRelations: {
    list: [],
    total: 0
  }, // 当前选中节点的全部关联列表
  searchRelations: {
    list: [],
    total: 0
  },
  relationsStatus: SUCCESS,
  createStatus: SUCCESS,
  deleteStatus: SUCCESS,
  currentCategoryGuid: null
};

const actions = {
  async getRelations(context, {categoryGuid, param}) {
    const {
      commit
    } = context;

    try {
      commit(GET_RELATION, categoryGuid);
      const res = await relationService.getRelations(categoryGuid, param);
      // 前端添加字段，为了较少改动一期代码
      res.categoryGuid = categoryGuid;
      commit(GET_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_RELATION_FAILURE, err);
      throw err;
    }
  },

  async getAllRelations(context, {categoryGuid}) {
    const {
      commit
    } = context;

    try {
      commit(GET_ALL_RELATION, categoryGuid);
      const res = await relationService.getRelations(categoryGuid, {
        offset: 0,
        limit: -1 // 传值为-1 查询全部数据
      });
      res.categoryGuid = categoryGuid;
      commit(GET_ALL_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_ALL_RELATION_FAILURE, err);
      throw err;
    }
  },

  async findRelations(context, {filterTableName, param}) {
    const {
      commit
    } = context;

    try {
      commit(SEARCH_RELATION, filterTableName);
      const res = await relationService.findRelations({ filterTableName, ...param });
      commit(SEARCH_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(SEARCH_RELATION_FAILURE, err);
      throw err;
    }
  },

  async createRelation(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_RELATION, params);
      const res = await relationService.create(params.categoryGuid, params.guids);
      commit(CREATE_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(CREATE_RELATION_FAILURE, err);
      throw err;
    }
  },

  async deleteRelation(context, params) {
    const {
      commit
    } = context;

    try {
      commit(DELETE_RELATION, params);
      const res = await relationService.remove(params.relations);
      commit(DELETE_RELATION_SUCCESS, res);
      return res;
    } catch (err) {
      commit(DELETE_RELATION_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  // 获取分层和表的分页关联关系
  [GET_RELATION](state) {
    state.relationsStatus = LOADING;
  },
  [GET_RELATION_SUCCESS](state, payload) {
    state.currentCategoryGuid = payload.categoryGuid;
    state.currentRelations = {
      list: payload.lists,
      total: payload.sum
    };
    state.relationsStatus = SUCCESS;
  },
  [GET_RELATION_FAILURE](state) {
    state.relationsStatus = FAIL;
  },
  // 获取分层和表的所有关联关系
  [GET_ALL_RELATION](state) {
    state.relationsStatus = LOADING;
  },
  [GET_ALL_RELATION_SUCCESS](state, payload) {
    state.currentAllRelations = {
      list: payload.lists,
      total: payload.sum
    };
    state.relationsStatus = SUCCESS;
  },
  [GET_ALL_RELATION_FAILURE](state) {
    state.relationsStatus = FAIL;
  },

  // 搜索表的关联关系
  [SEARCH_RELATION](state) {
    state.relationsStatus = LOADING;
  },
  [SEARCH_RELATION_SUCCESS](state, payload) {
    state.searchRelations = {
      list: payload.lists,
      total: payload.sum
    };
    state.relationsStatus = SUCCESS;
  },
  [SEARCH_RELATION_FAILURE](state) {
    state.relationsStatus = FAIL;
  },

  // 添加分层和表的关联关系
  [CREATE_RELATION](state) {
    state.createStatus = LOADING;
  },
  [CREATE_RELATION_SUCCESS](state, payload) {
    state.createStatus = SUCCESS;
  },
  [CREATE_RELATION_FAILURE](state) {
    state.createStatus = FAIL;
  },

  // 删除分层和表的关联关系
  [DELETE_RELATION](state) {
    state.deleteStatus = LOADING;
  },
  [DELETE_RELATION_SUCCESS](state, payload) {
    state.deleteStatus = SUCCESS;
  },
  [DELETE_RELATION_FAILURE](state) {
    state.deleteStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



