import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as metadataService from 'src/service/metadata-management/metadata-management-list';

const {
  GET_METADATA,
  GET_METADATA_SUCCESS,
  GET_METADATA_FAILURE,

  GET_TABLE,
  GET_TABLE_SUCCESS,
  GET_TABLE_FAILURE
} = types;

// initial state
const state = {
  metadatas: {
    list: [],
    total: 0,
    status: SUCCESS
  },
  tables: {
    list: [],
    total: 0,
    status: SUCCESS
  }
};

const actions = {
  async getMetadatas(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_METADATA, params);
      const res = await metadataService.getMetadataList(params);
      commit(GET_METADATA_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_METADATA_FAILURE, err);
      throw err;
    }
  },

  async getTableList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_TABLE, params);
      const res = await metadataService.getTableList(params);
      commit(GET_TABLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_TABLE_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [GET_METADATA](state, params) {
    let list = state.metadatas.list;

    if (params.offset === 0) {
      list = [];
    }
    state.metadatas = {
      list,
      status: LOADING
    };
  },
  [GET_METADATA_SUCCESS](state, payload, rootState) {
    const data = payload.lists.map(item => {
      const tableList = item.tableList || [];

      return {
        value: item.databaseId,
        label: item.databaseName,
        disabled: tableList.length === 0 || item.status === 'DELETED',
        children: tableList.map(item => {
          return {
            tableGuid: item.tableId,
            tableName: item.tableName,
            dbName: item.databaseName,
            status: item.status,
            value: item.tableId,
            label: item.tableName,
            disabled: item.status === 'DELETED',
            databaseName: item.databaseName
          };
        })
      };
    });
    const list = state.metadatas.list.concat(data);

    state.metadatas = {
      list,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_METADATA_FAILURE](state) {
    state.metadatas = {
      ...state.metadatas,
      status: FAIL
    };
  },

  [GET_TABLE](state, params) {
    let list = state.tables.list;

    if (params.offset === 0) {
      list = [];
    }
    state.tables = {
      list,
      status: LOADING
    };
  },
  [GET_TABLE_SUCCESS](state, payload, rootState) {
    const data = payload.lists.map(item => {
      return {
        tableGuid: item.tableId,
        tableName: item.tableName,
        dbName: item.databaseName,
        status: item.status,
        value: item.tableId,
        label: item.tableName,
        disabled: item.status === 'DELETED',
        databaseName: item.databaseName
      };
    });
    const list = state.tables.list.concat(data);

    state.tables = {
      list,
      total: payload.sum,
      status: SUCCESS
    };
  },
  [GET_TABLE_FAILURE](state) {
    state.tables = {
      ...state.tables,
      status: FAIL
    };
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



