import http from 'src/utils/http';

export function getMetadataList(param) {
  return http.post('metadata/search/database', param);
}

export function getTableList(param) {
  return http.post('metadata/search/table', param);
}

export function getColumnList(param) {
  return http.post('metadata/search/column', param);
}



