import axios from 'axios';
import {
  Message
} from '@gs-ui/gs-ui';
import config from 'src/config';
import store from 'src/store';

axios.defaults.baseURL = config.API_HOST;

axios.interceptors.request.use(function (conf) {
  const userInfo = store.state.user.userInfo;

  conf.headers[config.TICKET_KEY] = userInfo.Ticket;
  return conf;
});

axios.interceptors.response.use(undefined, err => {
  if (err) {
    const data = err.response.data || {};
    const status = err.response.status;

    if (status === 401) {
      axios.forwardLogin();
      return;
    }

    Message.error({
      content: data.errorMessage || err.message
    });
    return Promise.reject(err);
  }
});

axios.interceptors.response.use((res) => {
  return res.data;
});

axios.download = (formProps = { method: 'get' }, data = {}) => {
  return new Promise((resolve, reject) => {
    const form = document.createElement('form');
    const userInfo = store.state.user.userInfo;

    data = {
      ...data,
      [config.TICKET_KEY]: userInfo.Ticket
    };
    if (!formProps.action) {
      reject(new Error('action prop is required'));
    }
    if (!/^http(s)?:\/\//.test(formProps.action)) {
      formProps.action = `${axios.defaults.baseURL}${formProps.action}`;
    }

    Object.keys(formProps).forEach(key => {
      form.setAttribute(key, formProps[key]);
    });
    Object.keys(data).forEach(key => {
      const input = document.createElement('input');

      input.setAttribute('type', 'hidden');
      input.setAttribute('name', key);
      input.setAttribute('value', data[key]);
      form.appendChild(input);
    });

    document.body.appendChild(form);
    form.addEventListener('submit', (e) => {
      e.preventDefault();
      resolve();
    });
    form.submit();
    document.body.removeChild(form);
  });
};

axios.forwardLogin = () => {
  window.location.href = `${config.SSO_URL}?service=${window.location.href}`;
};

export default axios;



