<template>
  <ul class="top-nav">
    <li
      v-for="(item, index) in data"
      v-if="!item.hide"
      :key="item.path"
      :class="{'top-nav-item': true, 'is-active': isActive === item.path}"
      :title="isActive"
    >
      <a
        v-if="!item.children"
        :href="`#${item.path}`"
        class="top-nav-item-link"
      >
        <gs-icon
          v-if="item.icon"
          :name="item.icon"
        />
        <span>{{ item.title }}</span>
      </a>
      <gs-popover
        v-else
        :arrow="false"
        :ref="`popover${index}`"
        popper-class="top-nav-item-pop"
        trigger="hover"
        placement="bottom-start"
      >
        <ul class="top-nav-menu">
          <li
            v-for="(menu, mindex) in item.children"
            v-if="!menu.hide"
            :class="{'top-nav-menu-item': true, 'is-active': isActive === menu.path}"
            :key="menu.path"
          >
            <a
              v-if="!menu.children"
              :href="`#${menu.path}`"
              class="top-nav-menu-item-link"
              @click="handleClick(index)"
            >
              <gs-icon
                v-if="menu.icon"
                :name="menu.icon"
              />
              <span>{{ menu.title }}</span>
            </a>
            <gs-popover
              v-else
              :arrow="false"
              :append-to-body="false"
              :ref="`popovertwo${mindex}`"
              popper-class="top-nav-item-pop"
              trigger="hover"
              placement="right-start"
              @show="handleShowPop"
              @hide="handleHidePop"
            >
              <ul class="top-nav-menu">
                <li
                  v-for="link in menu.children"
                  v-if="!link.hide"
                  :class="{'top-nav-menu-item': true, 'is-active': isActive === link.path}"
                  :key="link.path"
                >
                  <a
                    :href="`#${link.path}`"
                    class="top-nav-menu-item-link"
                    @click="handleClickTwo(index, mindex)"
                  >
                    <gs-icon
                      v-if="link.icon"
                      :name="link.icon"
                    />
                    <span>{{ link.title }}</span>
                  </a>
                </li>
              </ul>
              <a
                slot="reference"
                :class="{'top-nav-menu-item-link': true, 'is-children-active': childrenActive(menu.children) || show}"
                href="javascript:void(0);"
              >
                <gs-icon
                  v-if="menu.icon"
                  :name="menu.icon"
                />
                <span>{{ menu.title }}</span>
                <gs-icon name="right" />
              </a>
            </gs-popover>
          </li>
        </ul>
        <a
          slot="reference"
          :class="{'top-nav-item-link': true, 'is-children-active': childrenActive(item.children)}"
          href="javascript:void(0);"
        >
          <gs-icon
            v-if="item.icon"
            :name="item.icon"
          />
          <span>{{ item.title }}</span>
        </a>
      </gs-popover>
    </li>
  </ul>
</template>

<script>
import {
  Popover
} from '@gs-ui/gs-ui';

export default {
  name: 'TopNav',

  components: {
    Popover
  },

  props: {
    data: {
      type: Array,
      default() {
        return [];
      }
    },
    defaultActive: {
      type: String,
      default: null
    },
    router: {
      type: Boolean,
      default: true
    }
  },

  data() {
    return {
      show: false
    };
  },

  computed: {
    isActive() {
      return this.defaultActive || (this.$route && this.$route.path);
    }
  },

  methods: {
    handleSelect(index, indexPath) {
      this.$emit('select', index, indexPath);
    },

    handleOpen(index, indexPath) {
      this.$emit('open', index, indexPath);
    },

    handleClose(index, indexPath) {
      this.$emit('close', index, indexPath);
    },

    handleClick(i) {
      const target = 'popover' + i;
      this.$refs[target][0].doClose();
    },

    handleClickTwo(i, j) {
      const targetone = 'popover' + i;
      const targettwo = 'popovertwo' + j;
      this.$refs[targetone][0].doClose();
      this.$refs[targettwo][0].doClose();
    },

    childrenActive(menus) {
      if (menus && menus.length) {
        // const activeMenu = menus.filter(item => {
        //   return item.path === this.isActive;
        // });
        let active = false;
        menus.map(item => {
          if (item.children && item.children.length) {
            item.children.map(child => {
              if (child.path === this.isActive) active = true;
            });
          } else {
            if (item.path === this.isActive) active = true;
          }
        });
        return active;
      } else {
        return false;
      }
    },

    handleShowPop() {
      this.show = true;
    },

    handleHidePop() {
      this.show = false;
    }
  }
};
</script>



