<template>
  <router-view v-if="success" />
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import 'src/components/style/global';
import http from 'src/utils/http';
import {
  checkLoading,
  checkSuccess
} from 'src/store/status';

export default {
  computed: {
    ...mapState('user', {
      authorizeStatus: 'authorizeStatus'
    }),

    loading() {
      return checkLoading(this.authorizeStatus);
    },

    success() {
      return checkSuccess(this.authorizeStatus);
    }
  },

  created() {
    this.authorize().catch(() => {
      http.forwardLogin();
    });
  },

  methods: {
    ...mapActions('user', [
      'authorize'
    ])
  }
};
</script>



