import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as fileService from 'src/service/file';

const {
  GET_FILE_LIST,
  GET_FILE_LIST_SUCCESS,
  GET_FILE_LIST_FAILURE,

  SEARCH_FILE_LIST,
  SEARCH_FILE_LIST_SUCCESS,
  SEARCH_FILE_LIST_FAILURE,

  UPLOAD_FILE,
  UPLOAD_FILE_SUCCESS,
  UPLOAD_FILE_FAILURE,

  DOWNLOAD_FILE,
  DOWNLOAD_FILE_SUCCESS,
  DOWNLOAD_FILE_FAILURE
} = types;

// initial state
const state = {
  list: {
    data: [],
    total: 0,
    status: SUCCESS
  }
};

const actions = {
  async getFileList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(GET_FILE_LIST, params);
      const res = await fileService.getFileList(params);
      commit(GET_FILE_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(GET_FILE_LIST_FAILURE, err);
      throw err;
    }
  },

  async searchFileList(context, params) {
    const {
      commit
    } = context;

    try {
      commit(SEARCH_FILE_LIST, params);
      const res = await fileService.searchFileList(params);
      commit(SEARCH_FILE_LIST_SUCCESS, res);
      return res;
    } catch (err) {
      commit(SEARCH_FILE_LIST_FAILURE, err);
      throw err;
    }
  },

  async uploadFile(context, params) {
    const {
      commit
    } = context;
    const {
      filePath,
      file
    } = params;
    const formData = new window.FormData();

    formData.append('filePath', filePath);
    formData.append('override', false);
    formData.append('file', file);

    try {
      commit(UPLOAD_FILE, formData);
      const res = await fileService.upload(formData);
      commit(UPLOAD_FILE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(UPLOAD_FILE_FAILURE, err);
      throw err;
    }
  },

  async downloadFile(context, filePath) {
    const {
      commit
    } = context;

    try {
      commit(DOWNLOAD_FILE, filePath);
      const res = await fileService.download(filePath);
      commit(DOWNLOAD_FILE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(DOWNLOAD_FILE_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  // list
  [GET_FILE_LIST](state) {
    state.list = {
      ...state.list,
      status: LOADING
    };
  },
  [GET_FILE_LIST_SUCCESS](state, payload) {
    state.list = {
      ...state.list,
      data: payload.list,
      total: payload.total,
      status: SUCCESS
    };
  },
  [GET_FILE_LIST_FAILURE](state) {
    state.list = {
      ...state.list,
      status: FAIL
    };
  },

  // search list
  [SEARCH_FILE_LIST](state) {
    state.list = {
      ...state.list,
      status: LOADING
    };
  },
  [SEARCH_FILE_LIST_SUCCESS](state, payload) {
    state.list = {
      ...state.list,
      data: payload.list,
      total: payload.total,
      status: SUCCESS
    };
  },
  [SEARCH_FILE_LIST_FAILURE](state) {
    state.list = {
      ...state.list,
      status: FAIL
    };
  },

  // upload
  [UPLOAD_FILE](state) {
    state.uploadStatus = LOADING;
  },
  [UPLOAD_FILE_SUCCESS](state, payload) {
    state.uploadStatus = SUCCESS;
  },
  [UPLOAD_FILE_FAILURE](state) {
    state.uploadStatus = FAIL;
  },

  // download
  [DOWNLOAD_FILE](state) {
    state.downloadStatus = LOADING;
  },
  [DOWNLOAD_FILE_SUCCESS](state, payload) {
    state.downloadStatus = SUCCESS;
  },
  [DOWNLOAD_FILE_FAILURE](state) {
    state.downloadStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



