import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import {
  list2tree,
  deleteInTree,
  updateInTree,
  sortTreeList,
  addTreeItem
} from 'src/utils';
import * as categoryService from 'src/service/category';

const {
  GET_CATEGORY,
  GET_CATEGORY_SUCCESS,
  GET_CATEGORY_FAILURE,

  CREATE_CATEGORY,
  CREATE_CATEGORY_SUCCESS,
  CREATE_CATEGORY_FAILURE,

  UPLEVEL_CATEGORY,
  UPLEVEL_CATEGORY_SUCCESS,
  UPLEVEL_CATEGORY_FAILURE,

  UPDATE_CATEGORY,
  UPDATE_CATEGORY_SUCCESS,
  UPDATE_CATEGORY_FAILURE,

  DELETE_CATEGORY,
  DELETE_CATEGORY_SUCCESS,
  DELETE_CATEGORY_FAILURE
} = types;

// initial state
const state = {
  categories: {
    list: [],
    status: LOADING
  },
  categoryMap: {},
  relations: {},
  currentCategoryGuid: null,
  categoryCreateStatus: SUCCESS,
  categoryUpdateStatus: SUCCESS,
  categoryDeleteStatus: SUCCESS
};

function res2category(res) {
  const {
    guid,
    name,
    parentCategoryGuid,
    description,
    upBrotherCategoryGuid,
    downBrotherCategoryGuid
  } = res;

  return {
    categoryGuid: guid,
    name,
    parentCategoryGuid,
    description,
    upBrotherCategoryGuid,
    downBrotherCategoryGuid
  };
}

const actions = {
  async getTopLevelCategory(context) {
    const {
      commit
    } = context;

    try {
      commit(GET_CATEGORY);
      const res = await categoryService.getCategory();
      // metaspace二期接口返回字段调整，手动添加一期字段categoryGuid，避免修改量过大
      res.forEach(item => {
        item.categoryGuid = item.guid;
      });
      const categoryMap = {};
      const unsortCategories = list2tree(res, 'categoryGuid', 'parentCategoryGuid');
      const categories = sortTreeList(unsortCategories, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
      res.forEach(item => {
        categoryMap[item.categoryGuid] = item;
      });
      commit(GET_CATEGORY_SUCCESS, { categories, categoryMap });
      return categories;
    } catch (err) {
      commit(GET_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async createCategory(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_CATEGORY, params);
      const res = await categoryService.create(params);
      const category = res2category(res);
      commit(CREATE_CATEGORY_SUCCESS, category);
      return category;
    } catch (err) {
      commit(CREATE_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async updateCategory(context, params) {
    const {
      commit
    } = context;

    try {
      commit(UPDATE_CATEGORY, params);
      const res = await categoryService.update(params);
      const category = res2category(res);
      commit(UPDATE_CATEGORY_SUCCESS, category);
      return category;
    } catch (err) {
      commit(UPDATE_CATEGORY_FAILURE, err);
      throw err;
    }
  },

  async deleteCategory(context, categoryGuid) {
    const {
      commit
    } = context;

    try {
      commit(DELETE_CATEGORY, categoryGuid);
      await categoryService.remove(categoryGuid);
      commit(DELETE_CATEGORY_SUCCESS, categoryGuid);
      return categoryGuid;
    } catch (err) {
      commit(DELETE_CATEGORY_FAILURE, categoryGuid);
      throw err;
    }
  }
};

const mutations = {
  // 获取一级目录
  [GET_CATEGORY](state) {
    state.categories = {
      ...state.categories,
      status: LOADING
    };
  },
  [GET_CATEGORY_SUCCESS](state, payload) {
    state.categories = {
      list: payload.categories,
      status: SUCCESS
    };
    state.categoryMap = payload.categoryMap;
  },
  [GET_CATEGORY_FAILURE](state) {
    state.categories = {
      ...state.categories,
      status: FAIL
    };
  },

  // 新建目录
  [CREATE_CATEGORY](state) {
    state.categoryCreateStatus = LOADING;
  },
  [CREATE_CATEGORY_SUCCESS](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    let list = categories.list;
    const parentCategoryGuid = payload.parentCategoryGuid;

    if (parentCategoryGuid) {
      const category = categoryMap[parentCategoryGuid];
      const children = category.children || [];

      payload.level = category.level + 1;
      category.children = addTreeItem(children, payload, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
    } else {
      payload.level = 0;
      list = addTreeItem(list, payload, 'categoryGuid', 'upBrotherCategoryGuid', 'downBrotherCategoryGuid');
    }
    state.categories = {
      ...categories,
      list: [ ...list ]
    };
    state.categoryMap = {
      ...categoryMap,
      [payload.categoryGuid]: payload
    };
    state.categoryCreateStatus = SUCCESS;
  },
  [CREATE_CATEGORY_FAILURE](state) {
    state.categoryCreateStatus = FAIL;
  },

  // 新建同级目录
  [UPLEVEL_CATEGORY](state) {
    state.categoryCreateStatus = LOADING;
  },
  [UPLEVEL_CATEGORY_SUCCESS](state) {
    state.categoryCreateStatus = SUCCESS;
  },
  [UPLEVEL_CATEGORY_FAILURE](state) {
    state.categoryCreateStatus = FAIL;
  },

  // 更新目录
  [UPDATE_CATEGORY](state) {
    state.categoryUpdateStatus = LOADING;
  },
  [UPDATE_CATEGORY_SUCCESS](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const categoryGuid = payload.categoryGuid;
    state.categoryMap = {
      ...categoryMap,
      [categoryGuid]: {
        ...categoryMap[categoryGuid],
        ...payload
      }
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, payload, 'categoryGuid')
    };
    state.categoryUpdateStatus = SUCCESS;
  },
  [UPDATE_CATEGORY_FAILURE](state) {
    state.categoryUpdateStatus = FAIL;
  },

  // 删除目录
  [DELETE_CATEGORY](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const category = categoryMap[payload];

    categoryMap[payload] = {
      ...category,
      deleting: true
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, categoryMap[payload], 'categoryGuid')
    };
    state.categoryDeleteStatus = LOADING;
  },
  [DELETE_CATEGORY_SUCCESS](state, payload) {
    const {
      categories
    } = state;

    state.categoryMap = {
      ...state.categoryMap,
      [payload]: null
    };
    state.categories = {
      ...categories,
      list: deleteInTree(categories.list, payload, 'categoryGuid')
    };
    state.categoryDeleteStatus = SUCCESS;
  },
  [DELETE_CATEGORY_FAILURE](state, payload) {
    const {
      categoryMap,
      categories
    } = state;
    const category = categoryMap[payload];

    categoryMap[payload] = {
      ...category,
      deleting: false
    };
    state.categories = {
      ...categories,
      list: updateInTree(categories.list, categoryMap[payload], 'categoryGuid')
    };
    state.categoryDeleteStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



