<template>
  <gs-header
    :has-toggler="false"
    :show-feedback="false"
    class="app-header"
    is-fixed
  >
    <span slot="logo">
      <span style="font-size: 20px; color: #fff">{{ title }}</span>
    </span>

    <top-nav
      slot="nav"
      :data="menus"
      :default-active="activeNav"
    />

    <ul slot="special">
      <li>
        <gs-dropdown
          class="user-info-dropdown"
          trigger="click"
          @select="handleSelect"
        >
          <span class="gs-dropdown-link">
            <span>{{ userInfo.DisplayName }}</span>
            <i class="gs-icon-down" />
          </span>
          <gs-dropdown-menu
            slot="dropdown"
            align="right"
          >
            <gs-dropdown-items :options="options" />
          </gs-dropdown-menu>
        </gs-dropdown>
      </li>
    </ul>
  </gs-header>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';
import TopNav from 'src/components/top-nav';
import {
  menus
} from 'src/router';
import config from 'src/config';
import http from 'src/utils/http';

export default {
  components: {
    TopNav
  },

  data() {
    return {
      title: config.TITLE,
      menus: menus[0].children,
      activeNav: '',
      options: [
        {
          label: '退出登录',
          value: 0
        }
      ]
    };
  },

  computed: {
    ...mapState('user', {
      userInfo: 'userInfo'
    })
  },

  watch: {
    '$route'(to, from) {
      const meta = to.meta;
      const index = meta.index;
      this.activeNav = index;
    }
  },

  mounted() {
    const current = this.$router.currentRoute;
    const meta = current.meta;
    const index = meta.index;
    this.activeNav = index;
  },

  methods: {
    ...mapActions('user', [
      'logout'
    ]),

    handleSelect(val) {
      switch (val) {
        case 0:
          this.logout().then(() => {
            http.forwardLogin();
          }, err => {
            this.$Message.error(err.message);
          });
          break;
        default: break;
      }
    }
  }
};
</script>



