import http from 'src/utils/http';

export function create(categoryGuid, param) {
  return http.post(`/metadata/category/${categoryGuid}/assignedEntities`, param);
}

export function remove(param) {
  return http.delete(`/metadata/category/relation`, { data: param });
}

export function getRelations(categoryGuid, param) {
  return http.post(`/metadata/category/relations/${categoryGuid}`, param);
}

export function findRelations(param) {
  return http.post(`/metadata/table/relations`, param);
}



