import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as userService from 'src/service/user';

const {
  AUTHORIZE,
  AUTHORIZE_SUCCESS,
  AUTHORIZE_FAILURE,

  LOGIN_OUT,
  LOGIN_OUT_SUCCESS,
  LOGIN_OUT_FAILURE
} = types;

// initial state
const state = {
  logoutStatus: SUCCESS,
  authorizeStatus: LOADING,
  userInfo: {}
};

const actions = {
  async authorize(context) {
    const {
      commit
    } = context;

    try {
      commit(AUTHORIZE);
      const res = await userService.authorize();
      commit(AUTHORIZE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(AUTHORIZE_FAILURE, err);
      throw err;
    }
  },


  async logout(context) {
    const {
      commit
    } = context;

    try {
      commit(LOGIN_OUT);
      const res = await userService.logout();
      commit(LOGIN_OUT_SUCCESS, res);
      return res;
    } catch (err) {
      commit(LOGIN_OUT_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [AUTHORIZE](state) {
    state.authorizeStatus = LOADING;
  },
  [AUTHORIZE_SUCCESS](state, payload) {
    state.userInfo = payload.data;
    state.authorizeStatus = SUCCESS;
  },
  [AUTHORIZE_FAILURE](state) {
    state.authorizeStatus = FAIL;
  },

  [LOGIN_OUT](state) {
    state.logoutStatus = LOADING;
  },
  [LOGIN_OUT_SUCCESS](state) {
    state.logoutStatus = SUCCESS;
  },
  [LOGIN_OUT_FAILURE](state) {
    state.logoutStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



