import http from 'src/utils/http';

export function formCreate(param) {
  return http.post('/table/create/form', param);
}

export function sqlCreate(param) {
  return http.post('/table/create/sql', param);
}

export function form2sql(param) {
  return http.post('/table/sql/format', param);
}

export function getTableSql(tableId) {
  return http.get(`/metadata/table/sql/${tableId}`);
}



