import SsoSdk from '@components/sso-jssdk';
import config from 'src/config';

const sdk = new SsoSdk({
  sso: config.SSO_API || `${config.SSO_URL}/api/v2`,
  key: config.TICKET_KEY
});

export function authorize() {
  return sdk.autoSSO();
}

export function logout(ticket) {
  return sdk.logout(ticket);
}



