<template>
  <gs-layout class="app-container">
    <!-- 头部区域 -->
    <basic-header slot="header" />

    <!-- 内容左侧菜单区域 -->
    <div
      slot="content"
      class="app-content"
    >
      <div class="app-page-content">
        <keep-alive include="MetadataManagement">
          <router-view />
        </keep-alive>
      </div>
    </div>
  </gs-layout>
</template>

<script>
import {
  Layout
} from '@gs-ui/gs-ui';
import BasicHeader from 'src/components/basic-header';

export default {
  components: {
    [Layout.name]: Layout,
    BasicHeader
  }
};
</script>



