import http from 'src/utils/http';

export function upload(param) {
  return http.post('/file/upload', param);
}

export function download(filePath) {
  return http.download({
    action: `/file/download`
  }, {
    filePath
  });
}

export function getFileList(param) {
  return http.post('/file/list', param);
}

export function searchFileList(param) {
  return http.post('/file/search', param);
}



