export function startWithRoot(path) {
  return /^\/.*/.test(path);
}

export function flexBlock(lg, sm, xs) {
  let data = {};

  function span(x) {
    return {
      span: x
    };
  }

  if (lg) {
    data.lg = span(lg);
  }
  if (sm) {
    data.sm = span(sm);
  }
  if (xs) {
    data.xs = span(xs);
  }

  return data;
}

export function list2tree(list, keyLabel = 'id', parentKeyLabel = 'parentId') {
  const map = {};
  const result = [];

  function travel(list, level = 0) {
    const tempList = [];

    if (!list || list.length === 0) {
      return;
    }

    list.forEach(item => {
      const parentId = item[parentKeyLabel];
      const id = item[keyLabel];

      if (!parentId) {
        item.level = level;
        result.push(item);
        map[id] = item;
      } else if (map[parentId]) {
        const parent = map[parentId];
        const children = parent.children || [];

        item.level = parent.level + 1;
        parent.children = children.concat([item]);
        map[item[keyLabel]] = item;
      } else {
        tempList.push(item);
      }
    });

    travel(tempList, ++level);
  }

  travel(list);
  return result;
}
/**
 * 为tree list 排序
 * @param {*} keyLabel item的键名称
 * @param {*} list tree list
 * @param {*} upKeyLabel 临近上游兄弟元素
 * @param {*} downKeyLabel 临近下游兄弟元素
 */
export function sortTreeList(list, keyLabel = 'id', upKeyLabel = 'upBrotherCategoryGuid', downKeyLabel = 'downBrotherCategoryGuid') {
  function sortArr(arr) {
    arr.forEach(item => {
      if (item.children && item.children.length > 0) {
        item.children = sortArr(item.children);
      }
    });
    let result = [];
    let map = {};
    let startItem = null;
    arr.forEach(item => {
      map[item[keyLabel]] = item;
      // 如果没有upKeyLabel值，证明是起始元素
      !item[upKeyLabel] && (startItem = item);
    });
    if (startItem) {
      result.push(startItem);
      while (startItem[downKeyLabel]) {
        startItem = map[startItem[downKeyLabel]];
        result.push(startItem);
      }
    }
    return result;
  }
  return sortArr(list);
}

export function deleteInTree(tree, deleteVal, keyLabel = 'value') {
  function travel(tree) {
    return tree.filter((item, i) => {
      const children = item.children;
      const isDel = item[keyLabel] === deleteVal;

      if (isDel) {
        return false;
      }
      if (children && children.length > 0) {
        item.children = travel(children);
      }
      return true;
    });
  }

  return travel(tree);
}

export function updateInTree(tree, updateItem, keyLabel = 'value') {
  function travel(tree) {
    return tree.map((item, i) => {
      const children = item.children;
      const isMatch = item[keyLabel] === updateItem[keyLabel];

      if (isMatch) {
        return {
          ...item,
          ...updateItem
        };
      }
      if (children && children.length > 0) {
        item.children = travel(children);
      }
      return item;
    });
  }

  return travel(tree);
}
/**
 * 将子节点插入到tree数组中
 * @param {*} arr tree数组
 * @param {*} item 插入元素
 * @param {*} upKeyLabel 兄节点label名称
 * @param {*} downKeyLabel 弟节点label名称
 */
export function addTreeItem(arr, item, keyLabel = 'id', upKeyLabel = 'upBrotherCategoryGuid', downKeyLabel = 'downBrotherCategoryGuid') {
  // 没用兄节点，说明是起始元素
  if (!item[upKeyLabel]) {
    arr.splice(0, 0, item);
  } else if (!item[downKeyLabel]) {
    // 没用弟节点，说明是最后元素
    arr.push(item);
  } else {
    for (let i = 0; i < arr.length; i++) {
      if (arr[i][keyLabel] === item[downKeyLabel]) {
        arr.splice(i, 0, item);
        break;
      }
    }
  }
  return arr;
}



