import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as tableService from 'src/service/metadata-management/table-create';

const {
  CREATE_TABLE,
  CREATE_TABLE_SUCCESS,
  CREATE_TABLE_FAILURE,

  FORM_2_SQL,
  FORM_2_SQL_SUCCESS,
  FORM_2_SQL_FAILURE,

  TABLE_SQL,
  TABLE_SQL_SUCCESS,
  TABLE_SQL_FAILURE
} = types;

// initial state
const state = {
  createStatus: SUCCESS,
  transformStatus: SUCCESS,
  transformSql: '',
  tableSql: '',
  tableSqlStatus: LOADING
};

const actions = {
  async createTableByForm(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_TABLE, params);
      const res = await tableService.formCreate(params);
      commit(CREATE_TABLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(CREATE_TABLE_FAILURE, err);
      throw err;
    }
  },

  async createTableBySql(context, params) {
    const {
      commit
    } = context;

    try {
      commit(CREATE_TABLE, params);
      const res = await tableService.sqlCreate(params);
      commit(CREATE_TABLE_SUCCESS, res);
      return res;
    } catch (err) {
      commit(CREATE_TABLE_FAILURE, err);
      throw err;
    }
  },

  async transformForm2Sql(context, params) {
    const {
      commit
    } = context;

    try {
      commit(FORM_2_SQL, params);
      const res = await tableService.form2sql(params);
      commit(FORM_2_SQL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(FORM_2_SQL_FAILURE, err);
      throw err;
    }
  },

  async getTableSql(context, tableId) {
    const {
      commit
    } = context;

    try {
      commit(TABLE_SQL, tableId);
      const res = await tableService.getTableSql(tableId);
      commit(TABLE_SQL_SUCCESS, res);
      return res;
    } catch (err) {
      commit(TABLE_SQL_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [CREATE_TABLE](state) {
    state.createStatus = LOADING;
  },
  [CREATE_TABLE_SUCCESS](state, payload) {
    state.createStatus = SUCCESS;
  },
  [CREATE_TABLE_FAILURE](state) {
    state.createStatus = FAIL;
  },

  [FORM_2_SQL](state) {
    state.transformStatus = LOADING;
  },
  [FORM_2_SQL_SUCCESS](state, payload) {
    state.transformStatus = SUCCESS;
    state.transformSql = payload.sql;
  },
  [FORM_2_SQL_FAILURE](state) {
    state.transformStatus = FAIL;
  },

  [TABLE_SQL](state) {
    state.tableSql = '';
    state.tableSqlStatus = LOADING;
  },
  [TABLE_SQL_SUCCESS](state, payload) {
    state.tableSqlStatus = SUCCESS;
    state.tableSql = payload.sql;
  },
  [TABLE_SQL_FAILURE](state) {
    state.tableSqlStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



