import types from 'src/store/mutation-types';
import {
  LOADING,
  SUCCESS,
  FAIL
} from 'src/store/status';
import * as cacheService from 'src/service/cache';

const {
  DATA_CACHE_REFRESH,
  DATA_CACHE_REFRESH_SUCCESS,
  DATA_CACHE_REFRESH_FAILURE
} = types;

// initial state
const state = {
  dataRefreshStatus: SUCCESS
};

const actions = {
  async refreshDataCache(context) {
    const {
      commit
    } = context;

    try {
      commit(DATA_CACHE_REFRESH);
      const res = await cacheService.refreshDataCache();
      commit(DATA_CACHE_REFRESH_SUCCESS, res);
      return res;
    } catch (err) {
      commit(DATA_CACHE_REFRESH_FAILURE, err);
      throw err;
    }
  }
};

const mutations = {
  [DATA_CACHE_REFRESH](state) {
    state.dataRefreshStatus = LOADING;
  },
  [DATA_CACHE_REFRESH_SUCCESS](state) {
    state.dataRefreshStatus = SUCCESS;
  },
  [DATA_CACHE_REFRESH_FAILURE](state) {
    state.dataRefreshStatus = FAIL;
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
};



